.spark-screen .spark-toggle {
  margin-left: -9999px;
  position: absolute;
  visibility: hidden;
}
.spark-screen .spark-toggle + label {
  cursor: pointer;
  outline: none;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.spark-screen input.spark-toggle-round-flat + label {
  background-color: #ccc;
  -webkit-border-radius: 60px;
  -moz-border-radius: 60px;
  -ms-border-radius: 60px;
  -o-border-radius: 60px;
  border-radius: 60px;
  height: 60px;
  padding: 2px;
  -webkit-transition: background 0.4s;
  -moz-transition: background 0.4s;
  -o-transition: background 0.4s;
  transition: background 0.4s;
  width: 120px;
}
.spark-screen input.spark-toggle-round-flat + label:before,
.spark-screen input.spark-toggle-round-flat + label:after {
  content: "";
  display: block;
  position: absolute;
}
.spark-screen input.spark-toggle-round-flat + label:before {
  background-color: #fff;
  -webkit-border-radius: 60px;
  -moz-border-radius: 60px;
  -ms-border-radius: 60px;
  -o-border-radius: 60px;
  border-radius: 60px;
  top: 2px;
  right: 2px;
  bottom: 2px;
  left: 2px;
  -webkit-transition: background 0.4s;
  -moz-transition: background 0.4s;
  -o-transition: background 0.4s;
  transition: background 0.4s;
}
.spark-screen input.spark-toggle-round-flat + label:after {
  background-color: #ccc;
  -webkit-border-radius: 52px;
  -moz-border-radius: 52px;
  -ms-border-radius: 52px;
  -o-border-radius: 52px;
  border-radius: 52px;
  top: 4px;
  bottom: 4px;
  left: 4px;
  -webkit-transition: margin 0.4s, background 0.4s;
  -moz-transition: margin 0.4s, background 0.4s;
  -o-transition: margin 0.4s, background 0.4s;
  transition: margin 0.4s, background 0.4s;
  width: 52px;
}
.spark-screen input.spark-toggle-round-flat:checked + label {
  background-color: #ccc;
}
.spark-screen input.spark-toggle-round-flat:checked + label:after {
  background-color: #ccc;
  margin-left: 60px;
}
